<template>
    <div class='dingdan'>
        <paypal
            amount="10.0"
            :client="credentials"
            env="12"
            :button-style="buttonStyle"
            @payment-authorized="paymentAuthorized"
            @payment-completed="paymentCompleted"
            @payment-cancelled="paymentCancelled"
        ></paypal>
        <div class="zhifu">
            <div class="title">支付方式
                <div class="hr"></div>
                <div class="zhifuList">
                    <div class="zhifuItem" :class="{active:active==1}" @click="active = 1">
                        paypal
                    </div>
                    <div class="zhifuItem" :class="{active:active==2}" @click="active = 2">1</div>
                    <div class="zhifuItem" :class="{active:active==3}" @click="active = 3">1</div>
                    <!-- <div class="tianjiaCard">
                        <div><img src="../../assets/img/shangcheng/blue.png" alt=""></div>
                        <div>添加银行卡</div>
                    </div> -->
                </div>
            </div>
        </div>
        <div class="mima">
            <div class="btn">确认支付</div>
        </div>
        <div></div>
    </div>
</template>
<script>
import PayPal from 'vue-paypal-checkout';
export default {
    data() {
        return {
            radio1:'',
            active:'',
            goodsList:'',
            credentials:{
                sandbox:'',
                production:'',
            },
            buttonStyle:{
                label:'pay',
                size:'small',
                shape:'rect',
                color:'blue'
            },
        };
    },
    created:function(){
    },
    mounted:function(){
        var _this = this;
        _this.imgUrl = _this.apis.second;
        _this.apis.post(_this.apis.order,{lang:_this.apis.lang,data:_this.$route.query.data}).then(function(res) {
            if(res.data.code == 0){
                _this.goodsList = res.data.data
            }
        })
    },
    watch:{
    },
    components:{
        paypal:PayPal
    },
    methods:{
        paymentAuthorized(data){
            // 授权完成的回调，可以拿到订单id
            console.log(data)
        },
        paymentCompleted(data){
            // 用户支付完成的回调，可以拿到订单id
            console.log(data)
        },
        paymentCancelled(data){
            // 用户取消交易的回调
            console.log(data)
        },
    },
}
</script>
<style lang='less' scoped>
@keyframes btnBg{
    0%{
        background: coral;
        border: 1px solid coral;
    }
    100%{
        background: none;
        border: 1px dashed #BCBCBC;
    }
}
.dingdan{
    width: 1200px;
    margin: 0 auto;
    .xinxi,.zhifu,.mima{
        margin-bottom: 50px;
        .title{
            font-size: 15px;
            color: #2D2D2D;
            font-weight: bold;
            .hr{
                width: 100%;
                height: 2px;
                margin-top: 24px;
                margin-left: 7px;
                background: #FFAD01;
            }
        }
    }
    .xinxi{
        h1{
            color: #2D2D2D;
            font-size: 15px;
            margin-bottom: 10px;
        }
        &>p{
            margin-top: 35px;
            margin-bottom: 10px;
            color: #4A4A4A;
            font-size: 13px;
            font-weight: bold;
        }
        .xinxiTitle{
            padding: 0 10px;
            margin-bottom: 35px;
            display: flex;
            justify-content: space-between;
            border-bottom: 2px solid #FFAD01;
            &>div{
                font-size: 13px;
                color: #757575;
                text-align: center;
            }
            &>div:nth-child(1){
                width: 260px;
            }
            &>div:nth-child(2){
                width: 180px;
            }
            &>div:nth-child(3){
                width: 160px;
            }
            &>div:nth-child(4){
                width: 140px;
            }
            &>div:nth-child(5){
                width: 200px;
            }
            &>div:nth-child(6){
                flex: 1;
            }
        }
        .hr:last-child{
            display: none;
        }
        &>div:nth-child(3){
            border-top: 1px solid #DEDEDE;
        }
        .proXinxi{
            display: flex;
            justify-content: space-between;
            align-items: top;
            padding: 10px 0px;
            margin: 0 10px;
            font-weight: bold;
            position: relative;
            .hr{
                position: absolute;
                bottom: 0;
                width: 95%;
                height: 1px;
                background: #DEDEDE;
            }
            &>div:nth-child(1){
                width: 260px;
                display: flex;
                justify-content: space-between;
                .img{
                    width: 62px;
                    height: 62px;
                    margin: 0 auto;
                }
                .text{
                    flex: 1;
                    height: 38px;
                    margin-left: 7px;
                    -webkit-line-clamp:2;
                    -webkit-box-orient: vertical;
                    display: -webkit-box;
                    text-overflow: ellipsis;
                    overflow: hidden;
                    color: #4A4A4A;
                    font-size: 13px;
                }
            }
            &>div:nth-child(2){
                width: 180px;text-align: center;
                color: #6D6C6C;
                font-size: 13px;
                img{
                    width: 55px;
                    height: 55px;
                    margin-left: 5px;
                }
            }
            &>div:nth-child(3){
                width: 160px;text-align: center;
                color: #6D6C6C;
            }
            &>div:nth-child(4){
                width: 140px;text-align: center;color: #6D6C6C;
            }
            &>div:nth-child(5){
                width: 200px;text-align: center;color: #FF6508;
            }
            &>div:nth-child(6){
                flex: 1;text-align: center;color: #FF6508;font-size: 18px;
            }
        }
        .xiadanXinxi{
            border-top: 1px solid #DEDEDE;
            padding-top: 20px;
            text-align: right;
            &>div:nth-child(1){
                font-weight: bold;
                text-align: right;
                &>span:nth-child(1){
                    color: #3B3B3B;
                    font-size: 15px;
                }
                &>span:nth-child(2){
                    color: #FF6508;
                    font-size: 18px;
                }
            }
            &>div:nth-child(2){
                width: 480px;
                height: 140px;
                display: inline-block;
                font-weight: bold;
                padding: 15px;
                border: 2px solid #FFAD77;
                .d1{
                    margin-bottom: 20px;
                    &>span:nth-child(1){
                        color: #474747;
                        font-size: 13px;
                    }
                    &>span:nth-child(2){
                        color: #FF6508;
                        font-size: 20px;
                    }
                    &>span:nth-child(3){
                        color: #FF6508;
                        font-size: 28px;
                    }
                }
                .d2{
                    color: #707070;
                    font-size: 12px;
                    margin-bottom: 8px;
                }
                .d3{
                    color: #707070;
                    font-size: 14px;
                    margin-bottom: 8px;
                }
            }  
            &>div:nth-child(3){
                margin-top: 10px;
                margin-bottom: 150px;
                .btn{ 
                    cursor:pointer; 
                    display: inline-block;
                    width: 184px;
                    height: 42px;
                    text-align: center;
                    line-height: 42px;
                    background: #FFAD01;
                    color: #fff;
                    font-size: 16px;
                }
            }
        }
    }
    .zhifu{
        .hr{
            margin-bottom: 20px;
        }
        .zhifuList{
            margin-left: 7px;
            .zhifuItem{
                height: 86px;
                margin: 10px 0;
                padding: 0 25px;
                border: 2px solid rgba(0, 0, 0, 0);
                border-bottom: 2px solid #DEDEDE;
            }
            .active{
                background: #FFE9DF;
                border: 2px solid #FFC777;
            }
        }
    }
    .mima{
        text-align: right;
        .btn{
            width: 184px;
            height: 42px;
            line-height: 42px;
            text-align: center;
            color: #fff;
            font-size: 16px;
            background: #FFAD01;
            display: inline-block;

        }
    }
}
</style>


